<template>
  <div class="progress-bar" ref="progressBar" @click="progressClick">
    <div class="bar-inner">
      <div class="progress" ref="progress"></div>
      <div class="progress-btn-wrapper" ref="progressBtn"

           @touchstart.prevent="progressTouchStart"
           @touchmove.prevent="progressTouchMove"
           @touchend="progressTouchEnd"
      >
      <!-- .prevent -->
        <div class="progress-btn"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
const progressBtnWidth = 16;//btn
	export default{
		name: 'progressBar',
		data(){
			return {

			}
		},
		created() {
			this.touch = {};
		},
		methods: {
			progressClick(e){
				const rect = this.$refs.progressBar.getBoundingClientRect()
        		const offsetWidth = e.pageX - rect.left
        		this._offset(offsetWidth)
        		//  progressBtn e.offsetX 
        		// this._offset(e.offsetX)
        		this._triggerPercent()
			},
			progressTouchStart(e){
				this.touch.initiated = true;
				// x
				this.touch.startX = e.touches[0].pageX;
				// 
				this.touch.left = this.$refs.progress.clientWidth;
			},
			progressTouchMove(e){
				// progressTouchStart
				if(!this.touch.initiated){
					return ;
				}
				// 
				const deltaX = e.touches[0].pageX - this.touch.startX;
				// 
				const offsetWidth = Math.max(0, this.touch.left + deltaX);
				// 
				const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth;
				const offsetW = Math.min(barWidth, offsetWidth);

				this._offset(offsetW);
			},
			progressTouchEnd(e){
				this.touch.initiated = false;

				this._triggerPercent();
			},
			_triggerPercent(){
				// 
				const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth;
				// progressWidth
				const progressWidth = this.$refs.progress.clientWidth;
				// 
				const percent = progressWidth / barWidth;

				// 
				this.$emit('percentChange',percent);
			},
			_offset(offsetW){
				// 
				this.$refs.progress.style.width = `${offsetW}px`;

				// 
				this.$refs.progressBtn.style.transform = `translate3d(${offsetW}px,0,0)`;
			}
		},
		props: {
			// 
			percent: {
				type: Number,
				default: 0
			}
		},
		// percent
		watch: {
			percent(newPercent){
				// console.log(newPercent);
				// this.touch.initiated
				if(newPercent>=0 && !this.touch.initiated){
					// 
					const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth;
					// btn
					const offsetWidth = newPercent * barWidth;

					// 
					this.$refs.progress.style.width = `${offsetWidth}px`;

					// 
					this.$refs.progressBtn.style.transform = `translate3d(${offsetWidth}px,0,0)`;
				}
			}
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .progress-bar
    height: 30px
    .bar-inner
      position: relative
      top: 13px
      height: 4px
      background: rgba(0, 0, 0, 0.3)
      .progress
        position: absolute
        height: 100%
        background: $color-theme
      .progress-btn-wrapper
        position: absolute
        left: -8px
        top: -13px
        width: 30px
        height: 30px
        .progress-btn
          position: relative
          top: 7px
          left: 7px
          box-sizing: border-box
          width: 16px
          height: 16px
          border: 3px solid $color-text
          border-radius: 50%
          background: $color-theme
</style>


