<template>
  <div class="singer" ref="singer">
    <!-- <list-view @select="selectSinger" :data="singers" ref="list"></list-view> -->
    <keep-alive>
    	<list-view :data='singers' @select='selectSinger' ref='list'></list-view>
    </keep-alive>
    <router-view></router-view>
  </div>
</template>

<script type="text/javascript">
import {getSingerList} from '../../api/singer';
import {ERR_OK} from '../../common/js/config';
import {Singer} from '../../common/js/singer';
import ListView from '../../base/listview/listview';
import {mapMutations} from 'vuex';
import playlistMixin from '../../api/mixin';
// 10
// A,B,C...26
const HOT_SINGER_LEN = 10
const HOT_NAME = ''
	export default{
		name: 'singer',
		mixins: [playlistMixin],
		data(){
			return {
				singers: [],
			}
		},
		created(){
			this._getSingerList();
		},
		methods:{
			//  mixinhandlerPlaylist
		    handlerPlaylist(list){
		       const bottom = list.length>0 ? '60px' : '';
		       this.$refs.singer.style.bottom = bottom;
		       this.$refs.list.refresh(); 
		    },
			...mapMutations({
		        setSinger: 'SET_SINGER'
		    }),
			// listview
			selectSinger(singer){
				// 
				this.$router.push({
					path: `/singer/${singer.id}`
				})
				// console.log(singer)
				// console.log(this.setSinger)
				// 
				// this.$store.commit('setSinger',singer);
				// 
				this.setSinger(singer)
			},
			// 
			_getSingerList(){
				getSingerList().then(res=>{
		          if(res.code===ERR_OK){
		          	console.log(res);
		            this.singers = this._normalizeSinger(res.data.list);
		            console.log(this.singers);
		          }
		        })
			},
			// 
			_normalizeSinger(list){
				// hot
				const map = {
					hot: {
						title: HOT_NAME,
						items: []
					}
				};
				// items
				list.forEach(function(item,index){
					// index10
					if(index<HOT_SINGER_LEN){
						map.hot.items.push(new Singer({
							id: item.Fsinger_mid,
							name: item.Fsinger_name
						}))
					}

					// 26
					const key = item.Findex;
					// key
					if(!map[key]){
						map[key] = {
							title: key,
							items: []
						}
					}
					// 
					map[key].items.push(new Singer({
						id: item.Fsinger_mid,
						name: item.Fsinger_name
					}))
				})
				//  map
				let ret = [];
				let hot = [];
				for(let key in map){
					const val = map[key];
					// 
					if(val.title.match(/[a-zA-Z]/)){
						ret.push(val);
					}
					if(val.title===HOT_NAME){
						hot.push(val);
					}
				}
				// 
				ret.sort((a, b) => {
		          return a.title.toLowerCase().charCodeAt(0) - b.title.toLowerCase().charCodeAt(0)
		        })
		        return hot.concat(ret);
			},
			
		},
		components: {
			ListView
		}

	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  .singer
    position: fixed
    top: 88px
    bottom: 0
    width: 100%
</style>


