<template>
  <scroll class="listview"  @scroll='scroll' 
                            :data='data' 
                            :listen-scroll="listenScroll" 
                            :probe-type="probeType"
                            
                            ref="listview">
    <!--  -->
    <ul>
      <li v-for="group in data" class="list-group" ref="listGroup">
        <h2 class="list-group-title">{{group.title}}</h2>
        <uL>
          <li @click="selectItem(item)" v-for="item in group.items" class="list-group-item">
            <img class="avatar" v-lazy="item.avatar">
            <span class="name">{{item.name}}</span>
          </li>
        </uL>
      </li>
    </ul>
    <!--  -->
    <div class="list-shortcut" @touchstart.stop.prevent="onShortcutTouchStart" @touchmove.stop.prevent="onShortcutTouchMove"
         @touchend.stop>
      <ul>
        <li v-for="(item, index) in shortcutList" :data-index="index" class="item"
            :class="{'current':currentIndex===index}">{{item}}
        </li>
      </ul>
    </div>
    <!--  -->
    <div class="list-fixed" ref="fixed" v-show="fixedTitle">
      <div class="fixed-title">{{fixedTitle}} </div>
    </div>
    <!-- loading -->
    <div v-show="!data.length" class="loading-container">
      <loading></loading>
    </div>
  </scroll>  
</template>

<script type="text/ecmascript-6">
import Scroll from '../../base/scroll/scroll';
import Loading from '../../base/loading/loading';
import {getData} from '../../common/js/dom';
  // 
  const TITLE_HEIGHT = 30
  // 
  const ANCHOR_HEIGHT = 18

  export default {
    name: 'listview',
    data(){
      return {
        scrollY: -1,//Y
        currentIndex: 0,//index
        diff: -1,//fixedTitle
      }
    },
    props: {
      data: {
        type: Array,
        default: []
      },

    },
    components: {
      Scroll,
      Loading
    },
    created(){
      // domthis
      // 
      this.touch = {};
      // 
      this.listenScroll = true;
      // 
      this.listHeight = [];
      // 
      this.probeType = 3;
    },
    computed: {
      shortcutList(){
        return this.data.map((group)=>{
          // 
          console.log(group.title.substring(0,1));
          return group.title.substring(0,1);
        })
      },
      fixedTitle(){
        // scroll
        // 
        if(this.scrollY>0){
          return '';
        }
        // this.datadata,singersingers
        return this.data[this.currentIndex] ? this.data[this.currentIndex].title : '';
      }
    },
    methods: {
      refresh(){
        this.$refs.listview.refresh();
      },
      // 
      selectItem(item){
        //  
        this.$emit('select',item);
      },
      // 
      onShortcutTouchStart(e){
        // 
        // 
        let target = e.target;
        let anchorIndex = getData(target,'index');
        // 
        let startTouch = e.touches[0];
        this.touch.startY = startTouch.pageY;
        // 
        this.touch.anchorIndex = anchorIndex

        this._scrollTo(anchorIndex);
        
        console.log(e);
      },
      // 
      onShortcutTouchMove(e){
        let moveTouch = e.touches[0];
        // 
        this.touch.endY = moveTouch.pageY;
        // y
        let _mY = this.touch.endY - this.touch.startY;
        // 
        this.touch.anchorIndex = parseInt(this.touch.anchorIndex) + Math.floor(_mY / ANCHOR_HEIGHT);
        let anchorIndex = this.touch.anchorIndex;
        this._scrollTo(anchorIndex);
      },
      // scroll
      scroll(pos){
        this.scrollY = pos.y
        console.log('listviewscroll');
      },

      // 
      _calculateHeight(){
        // listGroup
        this.listHeight = [];
        // listGroup
        let list = this.$refs.listGroup;
        let height = 0;
        this.listHeight.push(height);
        // 
        for(let i=0; i<list.length; i++){
          let item = list[i];
          height += item.clientHeight;
          this.listHeight.push(height);
        }
      },
      // 
      _scrollTo(anchorIndex){
        // 
        // 
        console.log(anchorIndex);
        if(anchorIndex<0){
          anchorIndex = 0;
        }
        if(anchorIndex>this.$refs.listGroup.length-2){
          anchorIndex = this.$refs.listGroup.length - 2;
        }
        // scrollY
        this.scrollY = -this.listHeight[anchorIndex];
        this.$refs.listview.scrollToElement(this.$refs.listGroup[anchorIndex],0);
      },
    },
    // data
    watch: {
      data(){
        setTimeout(()=>{
          this._calculateHeight();
        },20)
      },
      scrollY(newY){
        console.log(1);
        // 
        const listHeight = this.listHeight;
        console.log('listHeight'+this.listHeight)
        // 
        if(newY>0){
          this.currentIndex = 0;
          return ;
        }
        console.log(2);
        // 
        for(let i=0; i<listHeight.length - 1; i++){
          let height1 = listHeight[i];
          let height2 = listHeight[i+1];
          console.log(3);
          // height1height2currentIndex = i;
          // !height2height2undefined
          if(!height2 || ((-newY)>=height1 && (-newY)<height2)){
            this.currentIndex = i;
            console.log(""+this.currentIndex);
            return ;
          }
        }
        // -newY
        this.currentIndex = listHeight.length - 2
      },
      // fixedTitle
      diff(newVal){
        // top
        console.log("diff"+newval)
        const fixedTop = (newVal>0 && newVal<TITLE_HEIGHT) ? newVal - TITLE_HEIGHT : 0;
        if(this.fixedTop===fixedTop){
          return ;
        }
        this.fixedTop = fixedTop;
        // 
        this.$refs.fixed.style.transform = `translate3d(0,${fixedTop}px,0)`
      }
    }
  }

</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .listview
    position: relative
    width: 100%
    height: 100%
    overflow: hidden
    background: $color-background
    .list-group
      padding-bottom: 30px
      .list-group-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
      .list-group-item
        display: flex
        align-items: center
        padding: 20px 0 0 30px
        .avatar
          width: 50px
          height: 50px
          border-radius: 50%
        .name
          margin-left: 20px
          color: $color-text-l
          font-size: $font-size-medium
    .list-shortcut
      position: absolute
      z-index: 30
      right: 0
      top: 50%
      transform: translateY(-50%)
      width: 20px
      padding: 20px 0
      border-radius: 10px
      text-align: center
      background: $color-background-d
      font-family: Helvetica
      .item
        padding: 3px
        line-height: 1
        color: $color-text-l
        font-size: $font-size-small
        &.current
          color: $color-theme
    .list-fixed
      position: absolute
      top: 0
      left: 0
      width: 100%
      .fixed-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
    .loading-container
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



