<template>
	<div ref="wrapper">
		<slot></slot>
	</div>
</template>

<script type="text/javascript">
import BetterScroll from 'better-scroll';
export default{
	name: 'scroll',
	data(){
		return {

		}
	},
	props: {
		// scroll
		//  probeType  1 scroll 
		probeType: {
			type: Number,
			default: 1
		},
		click: {
			type: Boolean,
			default: true,
		},
		data: {
			type: Array,
			default: null
		},
		refreshDelay: {
			type: Number,
			default: 20
		},
		listenScroll: {
			type: Boolean,
			default: false
		},
		// 
		pullup: {
			type: Boolean,
			default: false
		},
		// 
		beforeScroll: {
			type: Boolean,
			default: false
		}
	},
	mounted(){
		setTimeout(()=>{
			this._initScroll();
		},this.refreshDelay)
	},
	methods: {
		_initScroll(){
			// wrapper
			if (!this.$refs.wrapper) {
	          return
	        }
			this.scroll = new BetterScroll(this.$refs.wrapper,{
				probeType:this.probeType,
				click: this.click
			});

			if(this.listenScroll){
				let that = this;
				// scroll
				this.scroll.on('scroll',(pos)=>{
					console.log("scroll"+pos);
					that.$emit('scroll',pos);//
				})
			}
			// 
			if(this.pullup){
				console.log('pullup')
				this.scroll.on('scrollEnd',()=>{
					if(this.scroll.y <= (this.scroll.maxScrollY + 50)){
						// 
						// 
						this.$emit('scrollToEnd');
						console.log(1)
					}
				})
			}

			if(this.beforeScroll){
				this.scroll.on('breforeScrollStart',()=>{
					this.$emit('beforeScroll');
				})
			}
		},
		enable(){
			// scroll
			// better-scroll
			this.scroll && this.scroll.enable();
		},
		disable(){
			// scroll
			// better-scroll
			this.scroll && this.scroll.disable();
		},
		refresh(){
			// scroll
			// better-scroll
			this.scroll && this.scroll.refresh();
		},
		// 
		scrollTo(){
			this.scroll && this.scroll.scrollTo.apply(this.scroll,arguments);
		},
		// 
		scrollToElement(){
			this.scroll && this.scroll.scrollToElement.apply(this.scroll,arguments);
		}
	},
	watch:{
		data(){
			setTimeout(()=>{
				this.refresh();
			},this.refreshDelay)
		}
	}
}
</script>


