<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot>
        <!-- slider -->
      </slot>
    </div>
    <div class="dots">
      <span class="dot" :class="{active: currentPageIndex === index }" v-for="(item, index) in dots"></span>
    </div>
  </div>
</template>

<script type="text/javascript">
import BetterScroll from 'better-scroll';
import {addClass} from '../../common/js/dom';
	export default{
		name: 'slider',
		data(){
			return{
        dots: [],
        currentPageIndex: 0
			}
		},
    // 
    props: {
      loop: {
        type: Boolean,
        default: true
      },
      autoplay: {
        type: Boolean,
        default: true
      },
      interval: {
        type: Number,
        default: 2000
      }
    },

    // domsliderGroup
    mounted(){
      // 
      setTimeout(()=>{
        // 
        this._setSliderWidth(false);
        // 
        this._initDots();
        // slider
        this._initSlider();
        // 
        if(this.autoplay){
          this._play();
        }

        // slider
        window.addEventListener("resize",()=>{
          if(!this.slider){
            return ;//this.slider
          }
          this._setSliderWidth(true);
          this.slider.refresh();
        });
      },20);
    },

    // 
    methods:{
      _setSliderWidth(isResize){
        // ref
        this.sliderGroupChildren = this.$refs.sliderGroup.children;
        let width = 0;//
        // 
        let sliderClientWidth = this.$refs.slider.clientWidth;
        // 
        let len = this.sliderGroupChildren.length;
        for(let i=0; i<len; i++){
          let child = this.sliderGroupChildren[i];
          // 
          addClass(child,"slider-item");
          // 
          child.style.width = sliderClientWidth + 'px';
          // 
          width += sliderClientWidth;
        }

        if(this.loop && !isResize){//
          width += sliderClientWidth * 2;//
        }

        // 
        this.$refs.sliderGroup.style.width = width + 'px';

      },
      _initSlider(){

        this.slider = new BetterScroll(this.$refs.slider,{
          scrollX: true,//
          scrollY: false,//
          momentum: false,//
          snap: { // slider
            loop: this.loop, // 
            threshold: 0.3, // 
            speed: 400 // 
          },
          bounce: true,
          stopPropagation: true,//
          click: true//a
        })
        console.log(this.slider)
        // 
        this.slider.on('scrollEnd',()=>{
          let pageIndex = this.slider.getCurrentPage().pageX;
          // pageIndex
          // if
          this.currentPageIndex = pageIndex;

          // console.log(pageIndex);
          // 
          if(this.loop){
            pageIndex -= 1;
          }

          // 
          if(this.autoplay){
            clearTimeout(this.timer);
            this._play();
          }
        })

        // 
        // 
        this.slider.on('beforeScrollStart', () => {
          if (this.autoPlay) {
            clearTimeout(this.timer)
          }
        })
      },

      //  
      _initDots(){
        this.dots = new Array(this.sliderGroupChildren.length);
      },

      // 
      _play(){
        // 1
        let pageIndex = this.currentPageIndex;
        // 
        // 1
        if(this.loop){
          pageIndex += 1;
          // 
          // 
          if(pageIndex>=5){
            pageIndex = 0;
          }
        }

        this.timer = setTimeout(()=>{
          // ,goToPage x,y,time,easing
          // x
          // y
          // time
          // easing
          this.slider.goToPage(pageIndex,0,400,{});
        },this.interval);
      }
    }
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slider
    min-height: 1px
    .slider-group
      position: relative
      overflow: hidden
      white-space: nowrap
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      top: 90%
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        &.active
          width: 20px
          border-radius: 5px
          background: $color-text-ll
</style>


